import sign from './sign';
import system from './message/system';
// import wx from 'we-jssdk'
export default {
    config: null,
    isInit : false,

    menuQQ:'menuItem:share:qq',
    menuWeibo:'menuItem:share:weiboApp',
    menuPengyouquan:'menuItem:share:timeline',
    menuPengyou:'menuItem:share:appMessage',

    exe(func){
        if(this.isInit){
            func();
        }else{
            this.wxinit(func);
        }
    },

    wxinit(func){
        system.weixinConfig().then((data)=>{
            this.config = {
                appid : data.appid,
                jsapi_ticket: data.ticket,
                url : window.location.href.split('#')[0]

            };
            console.log('config url :' + this.config.url);
            this.config.signResult = sign(this.config.jsapi_ticket, this.config.url);
            wx.ready(()=>{
                this.isInit = true;
                console.log('sdk!');
                console.log(...arguments);
                func && func(this.config);
            });
            wx.error((res)=>{
                console.log(res);
            });
            let settings = {
                appId: this.config.appid,
                timestamp: parseInt(this.config.signResult.timestamp),
                nonceStr: this.config.signResult.nonceStr,
                signature: this.config.signResult.signature,
                jsApiList: [
                    'openWXDeviceLib',
                    'configWXDeviceWiFi',
                    'checkJsApi',
                    'onMenuShareTimeline',
                    'onMenuShareAppMessage',
                    'onMenuShareQQ',
                    'onMenuShareWeibo',
                    'hideMenuItems',
                    'showMenuItems',
                    'hideAllNonBaseMenuItem',
                    'showAllNonBaseMenuItem',
                    'translateVoice',
                    'startRecord',
                    'stopRecord',
                    'onRecordEnd',
                    'playVoice',
                    'pauseVoice',
                    'stopVoice',
                    'uploadVoice',
                    'downloadVoice',
                    'chooseImage',
                    'previewImage',
                    'uploadImage',
                    'downloadImage',
                    'getNetworkType',
                    'openLocation',
                    'getLocation',
                    'hideOptionMenu',
                    'showOptionMenu',
                    'closeWindow',
                    'scanQRCode',
                    'chooseWXPay',
                    'openProductSpecificView',
                    'addCard',
                    'chooseCard',
                    'openCard'
                ]
            };
            wx.config(settings);

        }).catch((code) =>{
        });



    },
    scanQRCode(needResult, succ, finallydo){
        needResult = needResult ? 1 : 0;
        wx.scanQRCode({
            needResult: needResult, // 01
            scanType: ["qrCode","barCode"], // 
            success: function (res) {
                if(needResult){
                    succ(res.resultStr);
                }
            },
            complete(){
                finallydo && finallydo();
            },
            fail(res){
                console.log('scanQRCode:')
                console.log(res)
            }
        });
    },
    pay(func){
        wx.chooseWXPay({
            timestamp: timestamp, // jssdktimestamptimeStampS
            nonceStr: '', //  32 
            package: '', // prepay_idprepay_id=***
            signType: '', // 'SHA1''MD5'
            paySign: '', // 
            success: (res)=> {
                // 
                func(res);
            }
        })
    },
    previewImage(current,urls){
        wx.previewImage({
            current: current, // http
            urls: urls // http
        });
    },
    chooseImage(options){
        options = options || {};
        options.count = options.count || 1;
        let promse = new Promise((success, fail)=>{
            wx.chooseImage({
                count: options.count,
                success(res) {
                    var localIds = res.localIds; // IDlocalIdimgsrc
                    success(localIds);
                }
            });
        });
        return promse;

    },
    uploadImage(Ids, func){
        console.log("uploadImage");
        var sids = [];
        var lenght = Ids.lenght;
        var upload = function(index){
            Ids[index] && wx.uploadImage({
                localId: Ids[index], // IDchooseImage
                isShowProgressTips: 1, // 1
                success: function (res) {
                    var serverId = res.serverId; // ID
                    sids.push({id:Ids[index],sid:serverId});
                    if(sids.length < lenght){
                        func({event:'upone',data:{id:Ids[index],sid:serverId}});
                        upload(index+1);
                    }else{
                        func({event:'upend',data:sids});
                    }
                }
            });
        };
        upload(0);

    },
    downloadImage(){
        wx.downloadImage({
            serverId: '', // IDuploadImage
            isShowProgressTips: 1, // 1
            success: function (res) {
                var localId = res.localId; // ID
            }
        });
    },
    networkType(func){
        wx.getNetworkType({
            success (res) {
                func && func(res.networkType);// 2g3g4gwifi
            }
        });
    },
    getLocation(func){
        wx.getLocation({
            type: 'wgs84', // wgs84gpsopenLocation'gcj02'
            success: function (res) {
                var latitude = res.latitude; // 90 ~ -90
                var longitude = res.longitude; // 180 ~ -180
                var speed = res.speed; // /
                var accuracy = res.accuracy; // 
                func({lat:latitude,lng:longitude,speed:speed,accuracy:accuracy});
            },
            fail(res){
                console.log('getLocation fail');
                console.log(res);
            }
        });
    },
    sharepengyouqiang(c){
        wx.error((res)=>{
            console.log(res);
        });
        console.log('pengyouqiang');
        wx.onMenuShareTimeline({
            title: c.title, // 
            link: c.link, // 
            imgUrl: 'http://app.popled.cn/static/logo.png', // 
            success: function () {
                // 
            },
            cancel: function () {
                // 
            },
            complete(res){
                console.log(res);
            }
        });
        console.log('pengyouqiang end');
    },
    sharepengyou(c){
        wx.error((res)=> {
            console.log(res);
        });
        console.log('pengyou');
        wx.onMenuShareAppMessage({
            title: c.title, // 
            desc: c.desc, // 
            link: c.link, // 
            imgUrl: 'http://app.popled.cn/static/logo.png', // 
            type: c.type | 'link', // ,musicvideolinklink
            dataUrl: '', // typemusicvideo
            success: function () {
                // 
            },
            cancel: function () {
                // 
            },
            complete(res){
                console.log(res);
            }
        });
    },
    hideMenuItems(lists){
        wx.hideMenuItems({
            menuList: lists // menu3
        });
    },

    showMenuItems(lists){
        wx.showMenuItems({
            menuList: lists // menu3
        });
    },

};



